import { Component, OnInit } from '@angular/core';
import { data } from 'jquery';
import { RemoteServiceProvider } from '../providers/remote-service/remote-service';
import { LocalStorageService } from '../providers/local-storage/local-storage';
import { JwtTokenService } from '../providers/local-storage/jwt-token';
import { Router, ActivatedRoute } from "@angular/router";
import { AuthService } from '../providers/auth/auth.service';
import {NotificationeService} from '../providers/notification/notification'

declare var $: any;
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email:String = null;
  senha:String = null;

  constructor(
    private router: Router,
        private route: ActivatedRoute,
        private remoteService: RemoteServiceProvider,
        private jwtToken: JwtTokenService,
        private auth:AuthService,
        private notification:NotificationeService,
        private localStorage: LocalStorageService
  ) { }

  async login(){
    try{
      let login:any = await this.remoteService.doLogin(this.email, this.senha);
      console.log(login);
      if(!login.status) { throw new Error(login.message)}

      this.jwtToken.token = login.data.token;
      // this.localStorage.setObject('dadosUsuario',credential);
      this.auth.check = true;
      this.router.navigate(['/dashboard'], { relativeTo: this.route.parent });
    }
    catch(e){
      this.jwtToken.token = '';
      this.notification.show('top','center','danger','LOGIN',e);

    }

    

  }
  ngOnInit() {
    
  }

}
